import { Component, ChangeDetectionStrategy, NgZone, ChangeDetectorRef } from "@angular/core";
import { NavController, NavParams, Platform } from "ionic-angular";
import { Content } from "ionic-angular";

import { ElementRef, ViewChild } from "@angular/core";
import Magnifier from 'magnifier';

import { AppGlobals } from "@app/app.globals";
import { AppLocales } from "@app/app.locales";

import { MarsPictureService } from "@services/picture.service";
import { MarsInteractionService } from "@services/interaction.service";

import { Backend } from "@backend/index";


@Component({
    selector: "page-market-item-details",
    templateUrl: "market-item-details.html",
    changeDetection: ChangeDetectionStrategy.OnPush
})

export class MarketItemDetailsPage {

    @ViewChild("picture") picture: ElementRef;
    @ViewChild("content") content: Content;

    translations: AppTranslations;
    currentImg = "";
    slug: string;
    item: any;
    magnifier: any;

    constructor(private platform: Platform,
        private navCtrl: NavController,
        private navParams: NavParams,
        private zone: NgZone,
        private changeDetector: ChangeDetectorRef,
        private locales: AppLocales,
        private globals: AppGlobals,
        private pictureService: MarsPictureService,
        private interactionService: MarsInteractionService) {
        this.translations = this.locales.load();
    }

    ionViewWillEnter() {
        this.slug = this.navParams.get("slug");
        this.getMarketItemDetails();
    }

    ionViewDidEnter() {
        setTimeout(() => { this.content.scrollTo(0, 200, 200) }, 500);
    }

    async getMarketItemDetails() {
        this.zone.run(async () => {
            try {
                this.item = (await Backend.getMarketItemBySlug({ slug: this.slug })).data;
                this.currentImg = this.item.pictures[0];
            } catch (e) {
                this.interactionService.alert(this.translations.server_failure);
            } finally {
                this.changeDetector.detectChanges();
            }
        });

    }
}



